// ## fonts
// Save fonts to /src/fonts
// +font(book, cytiapro-black-webfont)
// and create mixin
// =book
// 	font-family: "book", Arial, sans-serif

body
	font-family: Roboto, Helvetica, Arial, sans-serif
	font-size: 14px
	line-height: 1.4
	font-weight: normal
a
	text-decoration: none
	+tr(all 0.3s)
	color: $basic
p
	color: #959595
	font-size: 14px
	font-weight: 400
	line-height: 20px
button
	position: relative
	border: 0
	outline: 0
	background-color: transparent 
.btn
	display: inline-block
	text-align: center
	color: #fff
	font: 700 14px/1 "Raleway", sans-serif
	font-family: "Raleway", sans-serif
	text-transform: uppercase
	height: 53px
	position: relative
	+tr(background-color 0.3s)
.btn-danger
	background-color: $active
	padding: 10px 28px
	&:hover
		background-color: lighten($active, 5%)
	&:active
		background-color: darken($active, 5%) 
.input
	color: #b7b7b7
	display: inline-block
	position: relative
	height: 53px
	line-height: 53px
	width: 100%
	padding: 0 28px
.form-row
	position: relative
.more-link
	position: relative
	font-size: 13px
	color: $active
	padding-bottom: 13px
	text-transform: uppercase
	display: inline-block
	&:hover
		&:after
			left: -8px
			right: -8px
			background-color: $active
	&:after
		content: ''
		display: block
		position: absolute
		left: 85px
		right: 85px
		bottom: 0 
		margin: auto
		height: 1px
		background-color: transparent 
		+tr(all 0.6s)
// common blocks
.container
	min-width: 1170px !important
	width: 1170px !important
	margin: auto
.title
	font-family: 'Raleway', sans-serif
	text-transform: uppercase
	color: #fff
	font-weight: 400
	line-height: 1
	&_bright
		background-color: $active
		color: #fff
		display: inline-block
		padding: 2px 4px
	&_dark
		color: $basic
//weigh
.title_ultra-bold
	font-weight: 900 
.title_extra
	font-weight: 800
.title_bold
	font-weight: 700
.title_semi
	font-weight: 600
//size
.title_ultra-large
	font-size: 98px
.title_very-large
	font-size: 60px
.title_large-x
	font-size: 48px
.title_large
	font-size: 46px
.title_medium
	font-size: 31px
.title_medium-s
	font-size: 30px
	
.price
	font-size: 18px
	font-weight: 900
	line-height: 20px
	display: inline-block
	vertical-align: middle
	&_old
		color: $gray
		font-size: 14px
		font-weight: 700
		text-decoration: line-through
	&_new
		color: $active
		font-size: 18px
		font-weight: 900
.badge
	position: absolute
	left: 0
	top: 30px
	z-index: 5
	transform: translate3d(0, 0, 0)
	&__item
		width: 61px
		height: 23px
		text-align: center
		display: block
		color: white
		font-size: 11px
		font-weight: 900
		font-style: italic
		padding: 3px 8px
		text-transform: uppercase
		white-space: nowrap
		overflow: hidden
		text-overflow: ellipsis
		&.is-sale
			background: url(../img/badge-sale.png) no-repeat center 
		&.is-new
			background: url(../img/badge-new.png) no-repeat center
		&.is-hot
			background: url(../img/badge-hot.png) no-repeat center
.rating
	position: absolute
	font-size: 0
	z-index: 2
	left: 0
	right: 0
	text-align: center
	.star
		display: inline-block
		vertical-align: middle
		+s(star)
		&.is-active
			+s(star-active)
		+ .star
			margin-right: 4px
.overlay
	position: absolute
	top: 0
	right: 0
	bottom: 0
	left: 0
	background-color: rgba(0, 0, 0, 0.7)
	z-index: 1


//p-list
.p-list
	list-style: decimal
	padding: 50px 80px
	font-size: 24px
	&__item
		margin-bottom: 10px
		a
			color: #000
			&:hover
				text-decoration: underline