.payments-list__item { display: inline-block; vertical-align: middle; }
.payments-list__item:not(:first-child) { margin-left: 15px; }

body { font-family: Roboto, Helvetica, Arial, sans-serif; font-size: 14px; line-height: 1.4; font-weight: normal; }

a { text-decoration: none; transition: all 0.3s; color: #363636; }

p { color: #959595; font-size: 14px; font-weight: 400; line-height: 20px; }

button { position: relative; border: 0; outline: 0; background-color: transparent; }

.btn { display: inline-block; text-align: center; color: #fff; font: 700 14px/1 "Raleway", sans-serif; font-family: "Raleway", sans-serif; text-transform: uppercase; height: 53px; position: relative; transition: background-color 0.3s; }

.btn-danger { background-color: #e5493a; padding: 10px 28px; }
.btn-danger:hover { background-color: #e85e51; }
.btn-danger:active { background-color: #e23423; }

.input { color: #b7b7b7; display: inline-block; position: relative; height: 53px; line-height: 53px; width: 100%; padding: 0 28px; }

.form-row { position: relative; }

.more-link { position: relative; font-size: 13px; color: #e5493a; padding-bottom: 13px; text-transform: uppercase; display: inline-block; }
.more-link:hover:after { left: -8px; right: -8px; background-color: #e5493a; }
.more-link:after { content: ""; display: block; position: absolute; left: 85px; right: 85px; bottom: 0; margin: auto; height: 1px; background-color: transparent; transition: all 0.6s; }

.container { min-width: 1170px !important; width: 1170px !important; margin: auto; }

.title { font-family: "Raleway", sans-serif; text-transform: uppercase; color: #fff; font-weight: 400; line-height: 1; }
.title_bright { background-color: #e5493a; color: #fff; display: inline-block; padding: 2px 4px; }
.title_dark { color: #363636; }

.title_ultra-bold { font-weight: 900; }

.title_extra { font-weight: 800; }

.title_bold { font-weight: 700; }

.title_semi { font-weight: 600; }

.title_ultra-large { font-size: 98px; }

.title_very-large { font-size: 60px; }

.title_large-x { font-size: 48px; }

.title_large { font-size: 46px; }

.title_medium { font-size: 31px; }

.title_medium-s { font-size: 30px; }

.price { font-size: 18px; font-weight: 900; line-height: 20px; display: inline-block; vertical-align: middle; }
