.badge { position: absolute; left: 0; top: 30px; z-index: 5; transform: translate3d(0, 0, 0); }
.badge__item { width: 61px; height: 23px; text-align: center; display: block; color: white; font-size: 11px; font-weight: 900; font-style: italic; padding: 3px 8px; text-transform: uppercase; white-space: nowrap; overflow: hidden; text-overflow: ellipsis; }
.badge__item.is-sale { background: url(../img/badge-sale.png) no-repeat center; }
.badge__item.is-new { background: url(../img/badge-new.png) no-repeat center; }
.badge__item.is-hot { background: url(../img/badge-hot.png) no-repeat center; }

.rating { position: absolute; font-size: 0; z-index: 2; left: 0; right: 0; text-align: center; }
.rating .star { display: inline-block; vertical-align: middle; background-image: url("../img/icons.png"); width: 13px; height: 13px; background-position: -17px -18px; }
.rating .star.is-active { background-image: url("../img/icons.png"); width: 13px; height: 13px; background-position: 0px -18px; }
