.overlay { position: absolute; top: 0; right: 0; bottom: 0; left: 0; background-color: rgba(0, 0, 0, 0.7); z-index: 1; }

.p-list { list-style: decimal; padding: 50px 80px; font-size: 24px; }
.p-list__item { margin-bottom: 10px; }
